/*
Reset.css
Updated: 2021-08-25
Author: Youni
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
button, input, select, textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
}

button, input {
  line-height: normal;
}

:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul, li {
  list-style: none;
}

li {
  display: list-item;
}

table {
  display: table;
  border-collapse: collapse;
  border-spacing: 0;
}

tr {
  display: table-row;
}

thead {
  display: table-header-group;
}

tbody {
  display: table-row-group;
}

tfoot {
  display: table-footer-group;
}

col {
  display: table-column;
}

colgroup {
  display: table-column-group;
}

td,
th {
  display: table-cell;
}

caption {
  display: table-caption;
}

th {
  font-weight: bolder;
  text-align: center;
}

caption {
  text-align: center;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

a {
  text-decoration: none;
}

a:focus,
a:active,
a:hover {
  outline: 0;
}

h1, h2, h3, h4, h5, h6,
b, strong {
  font-weight: bolder;
}

img {
  margin: 0;
  padding: 0;
  border: 0;
}

/* Begin bidirectionality settings (do not change) */
BDO[DIR=ltr] {
  direction: ltr;
  unicode-bidi: bidi-override;
}

BDO[DIR=rtl] {
  direction: rtl;
  unicode-bidi: bidi-override;
}

*[DIR=ltr] {
  direction: ltr;
  unicode-bidi: embed;
}

*[DIR=rtl] {
  direction: rtl;
  unicode-bidi: embed;
}

@media print {
  h1 {
    page-break-before: always;
  }

  h1, h2, h3, h4, h5, h6 {
    page-break-after: avoid;
  }

  ul, ol, dl {
    page-break-before: avoid;
  }
}
::-webkit-scrollbar {
  width: 12px;
  height: 8px;
  border: 3px solid #ffffff;
}

::-webkit-scrollbar-button:start:decrement,
::-webkit-scrollbar-button:end:increment {
  display: block;
  height: 10px;
  background: #ffffff;
}

::-webkit-scrollbar-track {
  background: #ffffff;
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  height: 50px;
  width: 50px;
  background: #ff8955;
  -webkit-border-radius: 8px;
  border-radius: 8px;
}

.v-application {
  font-family: "Maplestory", sans-serif !important;
}

@font-face {
  font-family: "Maplestory";
  font-weight: normal;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.eot?#iefix") format("embedded-opentype"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.woff2") format("woff2"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.woff") format("woff"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Maplestory";
  font-weight: bold;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.eot?#iefix") format("embedded-opentype"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.woff2") format("woff2"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.woff") format("woff"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.ttf") format("truetype");
  font-display: swap;
}
div {
  font-family: "Maplestory";
  color: #5e5e5e;
}

a {
  color: #5e5e5e;
}

a:link {
  color: #5e5e5e;
}

a:visited {
  color: #5e5e5e;
}

a:active {
  color: #5e5e5e;
}

.filter {
  padding-top: 58px;
  margin-bottom: 20px;
}

.review_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  border: 1px solid #eeeeee;
  margin: auto;
  height: auto;
}
.review_list .review_box {
  border: 1px solid #eeeeee;
  height: 291px;
}
.review_list .review_box .img_box {
  width: 100%;
  height: 200px;
  overflow: hidden;
}
.review_list .review_box .img_box img {
  width: auto;
  height: 100%;
}
.review_list .review_box .text_box dl {
  display: flex;
  flex-wrap: wrap;
  padding: 5px;
}
.review_list .review_box .text_box dl dd {
  width: 100%;
  text-align: left;
  font-size: 1em;
}
.review_list .review_box .text_box dl dd:first-child,
.review_list .review_box .text_box dl dd:nth-child(2),
.review_list .review_box .text_box dl dt {
  width: 50%;
  text-align: left;
}
.review_list .review_box .text_box dl dt {
  font-size: 1.2em;
  line-height: 1.6em;
  font-weight: bold;
}
.review_list .review_box .text_box dl #star {
  font-size: 2em;
  color: #ffe347;
  width: 50%;
  text-align: right;
}
.review_list .review_box .text_box dl dd:nth-of-type(3) {
  width: 90%;
}
.review_list .review_box .text_box dl dd:nth-of-type(4) {
  width: 10%;
}

@media all and (max-width: 767px) {
  .review_list {
    width: 100%;
    min-width: 300px;
  }
  .review_list .review_box {
    width: 70%;
    margin: auto;
  }

  .sort {
    flex-direction: column;
    font-size: 0.8em;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
  }
  .sort ul {
    display: flex;
    margin: auto;
    font-size: 1.1em;
    line-height: 3em;
  }
  .sort ul li a {
    padding: 0 10px;
  }
  .sort .search_box {
    padding-top: 5px;
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .sort .search_box .stage-search {
    margin: auto;
    width: 70%;
    font-size: 1em;
    line-height: 1.4em;
  }
}
@media all and (min-width: 768px) {
  .review_list {
    width: 45%;
    min-width: 700px;
  }
  .review_list .review_box {
    width: 33.33333%;
  }

  .sort {
    width: 45%;
    min-width: 700px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    height: 30px;
    margin-top: 20px;
    flex-direction: row;
  }
  .sort a {
    color: #5e5e5e;
    padding: 0 7px;
  }
  .sort ul {
    display: flex;
    justify-content: flex-start;
    line-height: 40px;
    width: 30%;
  }
  .sort ul li a:hover {
    cursor: pointer;
    color: #cf5724;
  }
  .sort .search_box {
    width: 45%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }
  .sort .search_box .stage-search {
    font-size: 1em;
    line-height: 1.4em;
    width: 70%;
    text-align: left;
  }
  .sort .search_box #search:hover {
    background: #ff8955;
    color: #fff;
    border-left: 0;
    border: 1px solid #cf5724;
  }
  .sort .search_box #reset:hover {
    color: #cf5724;
  }
}